.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.heroProb {
  position: absolute;
  margin: 0em 1em 3.5em 0.5em;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.heroIconGrid {
  flex-basis: 0 !important;
  max-width: max-content !important;
}

.heroCounterGrid {
}

@media screen and (min-width: 500px) {
  .heroQueueTwoBox {

  }
}
.heroQueueTwoBox {
  display: flex;
  flex-direction: column;
  margin-bottom: 3px;
  border-radius: 10px;
}

@media screen and (min-width: 800px) {
  .heroQueueTwoBox > div{
    display: flex;
    flex-direction: row;
  }
  .heroQueueTwoVSBox {
    grid-template-rows: 1fr 1fr 1fr !important;
    grid-template-columns: 1fr !important;
  }
  .heroQueueTwoVSPosition {
    justify-content: center;
    align-items: center;
    border-radius: 200%;
    text-align: center;
    display: flex;
    background-color: rgba(255,255,255,0.2);
    font-weight: bold;
  }
}

.heroButtonThreeDots{
  font-size: 2em;
}
.heroQueueTwoVSBox {
  margin: 2px;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
}

.heroQueueTwoVSPosition {
  justify-content: center;
  align-items: center;
  border-radius: 200%;
  text-align: center;
  display: flex;
  background-color: rgba(255,255,255,0.2);
  font-weight: bold;
}
.heroQueueTwoVSVS {
  justify-content: center;
  align-items: center;
  border-radius: 200%;
  text-align: center;
  display: flex;
  background-color: rgba(0,0,0,0.2);
  font-weight: bold;
}

.heroPackSelected {
  justify-content: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1em;
  max-width: min-content;
  margin: 0 0 0 0;
  border-radius: 10px;

}
.heroPackSelected div {
  margin: 2px
}

.heroCounterHeroGrid {
  margin: 0 0 0 0;
  display: flex;
  border-radius: 10px;
  flex-wrap: wrap;
}

.heroADBox {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.heroEditPackButton {
 /* width: 100%*/
}

.heroGrid {

}

.heroIcon {
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid white;
  max-height: 7em;
  max-width: 7em;
  width: 7em;
  height: 7em;
  min-height: 7em;
  min-width: 7em;
  /*height: 80px;
  width: 80px;*/
}

.closeIcon {
  max-height: 1em;
  max-width: 1em;
  width: 1em;
  height: 1em;
  min-height: 1em;
  min-width: 1em;
  margin-left: 0.2em;
  /*height: 80px;
  width: 80px;*/
}

.heroIconSpan {
  width: 100%;
  text-align: center;
}

.heroPackSelectedExtStatus {
  grid-column: 1 / 6;
  grid-row: 2 / 2;
  justify-self: center;
}